//GPS
import { apiGetPosition } from "@/api/api"
import { useUserPositionStore } from "@/store/global"
export const getCurrentLocation = () => {
    const userPositionStore = useUserPositionStore()
    if (userPositionStore.userPosition.cityId == 0) {
        try {
            window.ExtCallGetCurrentLocation = async (time, lat, long) => {
                // alert(`${time + "/" + lat + "/" + long}`)
                if (typeof (time) === "string" && time?.length > 6) {// 
                    const response = await apiGetPosition({ longitude: long, latitude: lat })
                    console.log('apiGetPosition' + '/' + response.data.defaultPlace.cityName + '/' + response.data.defaultPlace.townName)
                    //  cityId areaId
                    // pinia
                    userPositionStore.userPosition.cityId = response.data.defaultPlace.cityId
                    userPositionStore.userPosition.areaId = response.data.defaultPlace.areaId
                    userPositionStore.userPosition.cityId_2 = response.data.longTermCare2Place.cityId
                    userPositionStore.userPosition.areaId_2 = response.data.longTermCare2Place.areaId
                    console.log(userPositionStore.userPosition.cityId + '/' + userPositionStore.userPosition.areaId + '/' + userPositionStore.userPosition.cityId_2 + '/' + userPositionStore.userPosition.areaId_2   )
                }
            }
            ExtCallGPS.getCurrentLocation("ExtCallGetCurrentLocation")
        } catch (error) {
            console.log(' extCall GPS')
        }
    }
}



